import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, Historico } from '../../../../models/dtos/ger/historico';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface HistoricoEditViewProps extends EditViewProps<Historico> {}

const HistoricoEditView: FC<HistoricoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const { reset, ...formProps } = useForm<Historico>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const defaultWidth: number = 100;

  useEffect(
    () => {
      const { grupoHistorico } = globalReducer.globalParameter;
      setTimeout(() => {
        reset({
          ...selectedItem,
          grupoHistorico: {
            id: grupoHistorico.id,
          },
        });
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Histórico" wikiId={2136}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="grupoHistorico.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 6) } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código do Historico."
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 255, required: true } }}
              id="descricao"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição do Historico."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default HistoricoEditView;
